<template>
    <div ref="chart" class="task-chart" :style="{ width: '100%', height: '400px' }"></div>
  </template>
  
  <script>
  import * as echarts from 'echarts';
  
  export default {
    name: 'TaskChart',
    props: {
      tasks: {
        type: Array,
        required: true,
      },
    },
    mounted() {
      this.initChart();
      window.addEventListener('resize', this.resizeChart);
    },
    beforeUnmount() { // 使用 beforeUnmount 替代 beforeDestroy
      // 例如，清除定时器或取消事件监听
      console.log("TaskChart 组件即将卸载");
    },
    methods: {
      initChart() {
        const chartInstance = echarts.init(this.$refs.chart);
        const option = this.getChartOptions();
        chartInstance.setOption(option);
      },
      getChartOptions() {
        const deadlines = this.tasks.map(task => task.deadline); // 获取截止日期
        const priorities = this.tasks.map(task => task.priority); // 获取优先级
        const completionRates = this.tasks.map(task => task.completionRate); // 获取完成度
  
        return {
          title: {
            text: '待做事件的截止日期和优先级',
          },
          tooltip: {},
          xAxis: {
            type: 'category',
            data: deadlines,
          },
          yAxis: {
            type: 'value',
          },
          series: [
            {
              name: '优先级',
              type: 'bar',
              data: priorities,
            },
            {
              name: '完成度',
              type: 'line',
              data: completionRates,
            },
          ],
        };
      },
      resizeChart() {
        if (this.chartInstance) {
          this.chartInstance.resize();
        }
      },
    },
  };
  </script>
  
  <style scoped>
  .task-chart {
    border: 1px solid #ccc; /* 可选：给图表添加边框 */
  }
  </style>